import { createRouter, createWebHistory } from 'vue-router'
import Layout from '@/layout/IndexView'

const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [{
      path: '/home',
      meta: { title: '首页' },
      component: () => import('@/view/home/IndexView'),
    },
    {
      path: '/orderManage',
      meta: { title: '订单管理' },
      component: () => import('@/view/order/OrdersManage'),
    }]
  },
  {
    path: '/userCenter',
    component: Layout,
    redirect: '/userCenter',
    children: [{
      path: '/userCenter',
      meta: { title: '个人中心' },
      component: () => import('@/view/mine/IndexView')
    }]
  },
  {
    path: '/cardDetail',
    component: Layout,
    redirect: '/cardDetail',
    children: [{
      path: '/cardDetail',
      meta: { title: '接单详情' },
      component: () => import('@/view/home/components/CardDetail')
    }]
  },
  {
    path: '/orderDetail',
    component: Layout,
    redirect: '/orderDetail',
    children: [{
      path: '/orderDetail',
      meta: { title: '订单详情' },
      component: () => import('@/view/order/components/OrderDetail')
    }]
  },
  {
    path: '/fillUserInfo',
    component: Layout,
    redirect: '/fillUserInfo',
    children: [{
      path: '/fillUserInfo',
      meta: { title: '填写用户资料' },
      component: () => import('@/view/user/IndexView')
    }]
  },
  {
    path: '/login',
    component: Layout,
    redirect: '/login',
    children: [{
      path: '/login',
      meta: { title: '登陆' },
      component: () => import('@/view/login/IndexView')
    }]
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router